@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff"); }

*,
*::after,
*::before {
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  height: 100%;
  scroll-behavior: smooth; }

body {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  background-color: #ffffff; }

a {
  text-decoration: none;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

.container {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1023px) {
    .container {
      max-width: 1023px;
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 767px;
      padding: 0 15px; } }

.page-header__container {
  max-width: 1495px;
  padding-top: 33px;
  padding-bottom: 33px;
  background-color: saddlebrown;
  display: flex;
  flex: 1 1 auto;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 1023px) {
    .page-header__container {
      max-width: 768px;
      padding-top: 20px;
      padding-right: 11px;
      padding-bottom: 20px; } }
  @media (max-width: 767px) {
    .page-header__container {
      max-width: 287px;
      padding: 0;
      padding-top: 5px; } }

.page-header__logo {
  margin-right: auto; }
  @media (max-width: 767px) {
    .page-header__logo {
      width: 38px;
      height: 38px; } }

.page-header__button {
  padding: 13px 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-style: normal;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px; }
  @media (max-width: 1023px) {
    .page-header__button {
      display: none; } }

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-right: 38px; }
  @media (max-width: 1023px) {
    .main-nav__list {
      margin-right: 0;
      justify-content: center; } }

.main-nav__item {
  margin-right: 122px;
  margin-bottom: 5px; }
  .main-nav__item:last-child {
    margin-right: 0; }
  @media (max-width: 1023px) {
    .main-nav__item {
      margin-right: unset; } }

.main-nav__link {
  position: relative;
  display: block;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-style: normal;
  font-weight: 400;
  text-align: left;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .main-nav__link {
      padding: 12px 14px 0;
      font-size: 0; } }
  @media (max-width: 767px) {
    .main-nav__link {
      padding-top: 16px;
      padding-right: 0;
      padding-left: 23px; } }

.contacts-list__icon {
  position: absolute;
  top: 2px;
  left: -29px;
  width: 26px;
  height: 19px; }
  .contacts-list__icon--phone {
    top: 0;
    left: -26px;
    width: 24px;
    height: 24px; }
  @media (max-width: 1023px) {
    .contacts-list__icon {
      position: static;
      display: block; } }
